"use client";
import { useState } from "react";
import { Menu, X } from "lucide-react";

export default function Navbar() {
  const [open, setOpen] = useState(false);

  return (
    <header className="fixed top-0 inset-x-0 z-50 h-16 bg-transparent">
      <div className="mx-auto max-w-6xl h-full px-4 grid grid-cols-[1fr,auto,1fr] items-center">
        {/* Left links (desktop) */}
        <nav className="hidden md:flex gap-6">
          <a href="/about">About</a>
          <a href="/contact">Contact</a>
        </nav>

        {/* Center logo (always visible) */}
        <a href="/" className="justify-self-center font-heading text-2xl">
          holidaze
        </a>

        {/* Right area: desktop links + mobile hamburger */}
        <div className="justify-self-end flex items-center gap-6">
          <nav className="hidden md:flex gap-6">
            <a href="/venues">Venues</a>
            <a href="/auth/login">Login</a>
          </nav>

          {/* Hamburger (mobile only) */}
          <button
            className="md:hidden h-10 w-10 grid place-items-center rounded-lg
                       bg-white/10 hover:bg-white/15"
            aria-label="Open menu"
            onClick={() => setOpen((v) => !v)}>
            {open ? <X size={18} /> : <Menu size={18} />}
          </button>
        </div>
      </div>

      {/* Mobile sheet / menu */}
      {open && (
        <div
          className="md:hidden absolute top-16 inset-x-0 border-t border-white/10
                     bg-[#282A2E]/95 backdrop-blur">
          <nav className="px-4 py-3 flex flex-col gap-2 text-lg">
            <a href="/about" onClick={() => setOpen(false)}>
              About
            </a>
            <a href="/contact" onClick={() => setOpen(false)}>
              Contact
            </a>
            <a href="/venues" onClick={() => setOpen(false)}>
              Venues
            </a>
            <a href="/auth/login" onClick={() => setOpen(false)}>
              Login
            </a>
          </nav>
        </div>
      )}
    </header>
  );
}
